import React, {useState, useEffect} from "react";
import './styles.css'
import { createUseStyles } from "react-jss";
import bg from '../imgs/tvr_Mesa de trabajo 1.png'
import Link from "gatsby-link";

const useStyles = createUseStyles({
    container: {
        display: 'flex',
        overflow: 'hidden',
        '@media (max-width: 620px)': {
            alignItems: 'center',
            flexDirection: 'column',
        },
    },
});

const App = () => {
    const [counter, setCounter] = useState(0);
    const classes = useStyles();
    
    useEffect(() => {
        let intervalId = setInterval(() => {
            if (counter >= 360) {
                return setCounter(0)
            }
            console.log(counter)
            
            setCounter(counter => counter + 1);
        }, 10);
        
        return () => clearInterval(intervalId);
    });
    
    return (
        <div className={classes.container}>
            <div style={{
                position: 'absolute',
                backgroundImage: `url("${bg}")`,
                filter: `hue-rotate(${counter}deg)`,
                overflow: 'hidden',
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                paddingBottom: 50,
                width:'100vw',
                backgroundSize: 'contain',
                backgroundPositionX: -10,
                backgroundPositionY: -10,
                zIndex: -1,
            }}
            >
                <a href="http://www.gigaglitters.com/">
                    <img
                        src="http://www.gigaglitters.com/created/I9GyPXbmRp.gif"
                        width="330"
                        height="67"
                        border="0"
                        alt="http://www.rasaint.net/ - Glitter Graphics"
                    />
                </a>
                <Link to="/cuentos">
                    <img
                        style={{width: 200}}
                        src="https://web.archive.org/web/20091027073653/http://geocities.com/anyoldirons/bookgrey.gif"
                        alt=""
                    />
                </Link>
                <Link to="/el_cielo">
                    <img
                        style={{width: 200}}
                        src="https://web.archive.org/web/20091027152314/http://www.geocities.com/Paris/Lights/1439/painting/dovean.gif"
                        alt=""
                    />
                </Link>
                <Link to="/museo">
                    <img
                        style={{width: 200}}
                        src="https://web.archive.org/web/20091019142217/http://es.geocities.com/avilarte/images/painting.gif"
                        alt=""
                    />
                </Link>
            </div>
            <Link to="/acuario">
                <img
                    style={{width: '100vw', position: 'fixed', bottom:0, left:0}}
                    src="https://web.archive.org/web/20090830140518/http://geocities.com/Hollywood/Trailer/5731/anuncio.gif"
                    alt=""
                />
            </Link>
        </div>
    );
}

export default App
